<template>
<transition name="slide-fade">
	<div class="cc-main">
		<div class="cc-top" @click="company">
			<div>{{detail.name}}</div>
			<p>{{detail.sizeStr}} · {{detail.industry}}</p>
		</div>
		<div class="cc-tit">招聘管理</div>
		<van-pull-refresh class="cc-list0" v-model="refreshing" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
				<div class="cc-list" v-for="(item,index) in list" :key="index" @click="goto(item.jobId)">
					<div class="cc-name flexstart"><span>{{item.name}}</span><span>{{item.payStr}}</span></div>
					<div class="cc-address flexstart"><span>{{item.city}}</span><span>{{item.educationStr}}</span><span>招{{item.num}}人</span></div>
					<div class="cc-rlzy flexstart" v-if="item.enterpriseName">
						<img class="cc-l" src="../assets/img/vip1.png" alt="">
						<div class="cc-name van-ellipsis">{{item.enterpriseName}}</div>
						<a class="cc-r" :href="'tel:'+item.enterpriseMobile" v-on:click.stop><img class="cc-logo" src="../assets/img/phone5.png" alt=""></a>
					</div>
					<div class="cc-icon flexend"><span v-if="item.status==1">已关闭</span><van-icon color="#C6C9D2" name="arrow" /></div>
					
				</div>
			</van-list>
		</van-pull-refresh>
		<div class="cc-button"><div @click="goto1">发布招聘</div></div>
	</div>
</transition>
</template>
<script type="text/javascript">
import {PullRefresh,List,Icon,Toast} from 'vant';
export default {
	name: 'home',
	components: {
		[PullRefresh .name]: PullRefresh ,
		[List.name]: List,
		[Icon.name]: Icon,
		[Toast.name]: Toast,
	},
	data() {
		return {
			pageNum:1,
			pageSize:6,
			list: [],
			loading: false,
			finished: false,
			refreshing: false,
			detail:""
		};
	},
	mounted() {
		this.detailCompany();
	},
	methods: {
		goto(rt){
			this.$router.push({
				path:"/detail1?id="+rt
			})
		},
		company(){
			this.$router.push({
				path:"/company"
			})
		},
		goto1(){
			this.$router.push({
				path:"/publish"
			})
		},
		//渲染头部
		detailCompany() {
			var that=this;
			this.$http.get('/company/detailCompany').then(function (res) {
				if (res.code==10000) {
					that.detail=res.data;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//渲染列表
		onLoad() {
			if (this.refreshing) {
				this.list = [];
				this.refreshing = false;
			}
			var that=this;
			this.$http.get(`/company/myJobs/${that.pageNum}/${that.pageSize}`).then(function (res) {
				that.loading = false;
				if (res.code==10000) {
					that.list.push(...res.data.list)
					if (res.data.hasNextPage) {
						that.pageNum=res.data.pageNum+1
					}else{
						that.finished = true;
					}
				}else{
					that.finished = true;
					Toast.fail(res.msg);
				}
			}).catch(function () {
				that.loading = false;
			});
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.pageNum = 1;
			this.onLoad();
		},
	}
}
</script>
